.cat-info {
  margin-top: 25px;
  display: flex;
}

.breed-select {
  cursor: pointer;
  min-width: 1025px;
  border: 3px double #000;
  border-top-left-radius: 6px;
  border-bottom-right-radius: 6px;
}

.breed-select:hover, .breed-select:focus {
  outline: none;
}

.thumb {
  max-width: 600px;
  margin-left: 25px;
}

.thumb-pic {
  position: relative;
}

.thumb-pic:before {
  z-index: 2;
  content: "";
  width: 50%;
  height: 100%;
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, .3) 100%);
  display: block;
  position: absolute;
  top: 0;
  left: -75%;
  transform: skewX(-25deg);
}

.thumb-pic:hover:before {
  animation: shine .75s;
}

@keyframes shine {
  100% {
    left: 125%;
  }
}

